@import "../../global.scss";
.portfolio{
    height: fit-content;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 1000px;
  

    @include mobile{
        padding: 10rem 2rem 15rem 2rem;
    }
   

    h1{
        font-size: 50px;
        @include mobile{
            font-size: 20px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        
        @include mobile{
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
        }


    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transform: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 120px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.5;
                    z-index: 0;
                }
            }
        }

        
    }

}

